"use strict";
let videoStream: MediaStream | null = null;

async function getCameraDevices(): Promise<MediaDeviceInfo[]> {
    const devices = await navigator.mediaDevices.enumerateDevices();
    return devices.filter(device => device.kind === 'videoinput');
}

async function populateCameraDropdown() {
    const cameraDropdown = document.getElementById("cameraDropdown") as HTMLSelectElement | null;
    if (!cameraDropdown) {
        console.error("Camera dropdown not found");
        return;
    }

    const videoDevices = await getCameraDevices();
    videoDevices.forEach(device => {
        const option = document.createElement("option");
        option.value = device.deviceId;
        option.text = device.label || `Camera ${cameraDropdown.options.length + 1}`;
        cameraDropdown.appendChild(option);
    });
}

function insertImageToSlide(imageDataUrl: string) {
    console.log("Inserting image.");
    Office.context.document.setSelectedDataAsync(imageDataUrl, { coercionType: Office.CoercionType.Image }, function (asyncResult) {
        if (asyncResult.status === Office.AsyncResultStatus.Failed) {
            console.error("Error inserting image: " + asyncResult.error.message);
        } else {
            console.log("Image inserted successfully.");
        }
    });
}

async function capturePhoto() {
    let videoElement = document.querySelector("video") as HTMLVideoElement | null;
    let canvasElement = document.querySelector("canvas") as HTMLCanvasElement | null;
    if (!videoElement) {
        videoElement = document.createElement("video");
        document.body.appendChild(videoElement);
    }
    if (!canvasElement) {
        canvasElement = document.createElement("canvas");
        document.body.appendChild(canvasElement);
    }
    const captureButton = document.getElementById("capturePhotoButton");
    if (!captureButton) {
        console.error("Capture button not found");
        return;
    }
    if (!videoStream) {
        try {
            const cameraDropdown = document.getElementById("cameraDropdown") as HTMLSelectElement | null;
            if (!cameraDropdown) {
                console.error("Camera dropdown not found");
                return;
            }
            const selectedDeviceId = cameraDropdown.value;
            const stream = await navigator.mediaDevices.getUserMedia({
                video: {
                    deviceId: { exact: selectedDeviceId },
                    width: { ideal: 3712, max: 3712 }, // Set desired width
                    height: { ideal: 2784, max: 2784 }, // Set desired height
                    // eslint-disable-next-line @typescript-eslint/no-explicit-any
                    advanced: [{ focusMode: "continuous" } as any] // Enable autofocus with type assertion
                }
            });
            videoStream = stream;
            videoElement.srcObject = stream;
            videoElement.play();
        } catch (error) {
            console.error("Error accessing the camera", error);
        }
    }
    captureButton.onclick = function () {
        if (videoStream) {
            const desiredWidth = 3712; // Set desired width for high resolution
            const desiredHeight = 2784; // Set desired height for high resolution
            if (canvasElement) {
                canvasElement.width = desiredWidth;
                canvasElement.height = desiredHeight;
                const context = canvasElement.getContext("2d");
                if (context && videoElement) {
                    context.drawImage(videoElement, 0, 0, desiredWidth, desiredHeight);
                    const imageDataUrl = canvasElement.toDataURL("image/png").split(',')[1];
                    console.log("Captured image data URL:", imageDataUrl); 
                    insertImageToSlide(imageDataUrl);
                    context.clearRect(0, 0, canvasElement.width, canvasElement.height);
                } else {
                    console.error("Failed to get canvas context or video element.");
                }
            }
        } else {
            console.error("No video stream available");
        }
    };
}

function stopCamera() {
    if (videoStream) {
        videoStream.getTracks().forEach((track: MediaStreamTrack) => track.stop());
        videoStream = null;
        const videoElement = document.querySelector("video");
        if (videoElement) {
            videoElement.remove();
        }
        const canvasElement = document.querySelector("canvas");
        if (canvasElement) {
            canvasElement.remove();
        }
    }
}

Office.onReady(function () {
    const captureButton = document.getElementById("capturePhotoButton");
    if (captureButton) {
        captureButton.addEventListener("click", capturePhoto);
    }
    const stopButton = document.createElement("button");
    stopButton.textContent = "Stop Camera";
    document.body.appendChild(stopButton);
    stopButton.addEventListener("click", stopCamera);

    // Create and populate the camera dropdown
    const cameraDropdown = document.createElement("select");
    cameraDropdown.id = "cameraDropdown";
    document.body.appendChild(cameraDropdown);
    populateCameraDropdown();
});